.Field {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px solid #00d2d3;
  border-radius: 5px;
  font-size: 2rem;
  color: #00d2d3;
}

.finish,
.start {
  font-size: 1rem;
}

.start {
  color:  #10ac84;
}
.finish {
  color: #ee5253;
}

.special {
  border: 4px solid #ee5253;
  color: #ee5253;
}

.Player {
  position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate3d(-50%, -50%, 0);
  width: 70%;
  height: auto;
  object-fit: contain;
  animation: player 2s ease-in-out infinite;
  transition-property: opacity;
  transition-duration: 0.7s;
  transition-delay: 2s
}

@media screen and (min-width:700px){
  .Field {
    font-size: 4.5rem;
  }
  .finish,
  .start {
    font-size: 2.5rem;
  }
}

@keyframes player {
  0% {
    transform: scale(1) rotate();
  }
  25% {
    transform: scale(1) rotate(-10deg);
  }
  50% {
    transform: scale(1.1) rotate(10deg);
  }
}

@keyframes playerMove {
  0% {
    opacity: 1;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}